.infoContainer{
  display: flex;
  align-items: center;
  gap: 1rem;
  color: var(--textMainColor);
}
.img{
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
}
.img>:first-child{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  fill: var(--svgMainColor);
  stroke: var(--svgMainColor);
}
@media(max-width:1200px){
  .infoContainer{
    font-size: 1.4rem;
}
}