  margin-top: .5rem;
  background-color: black;
  height: 5rem;
}







@keyframes blinkingInsctiptin{
  0%{
    color: var(--mainBlock);
  }
  100%{
    color: var(--fontColorInblock);
  }
}
@keyframes topLine{
  0%{
    top: 0%;
  }
  25%{
    top: 50%;
    transform: rotate(0deg);
  }
  50%{
    top: 50%;
    transform: rotate(-30deg);
  }
  75%{
    top: 50%;
    transform: rotate(0deg);
  }
  100%{
    top: 0%;
  }
}
@keyframes bottomLine{
  0%{
    top: 100%;
  }
  25%{
    top: 50%;
    transform: rotate(0deg);
  }
  50%{
    top: 50%;
    transform: rotate(30deg);
  }
  75%{
    top: 50%;
    transform: rotate(0deg);
  }
  100%{
    top: 100%;
  }
}
@keyframes middleLine{
  0%{
    display: block;
  }
  1%{
    display: none;
  }
  99%{
    display: none;
  }
  
  100%{
    display: block;
  }
}
