.container{
  display: flex;
  align-items: center;
  gap: 1rem;
}
.levels{
  display: flex;
  align-items: center;
  gap: .2rem;
}
.name{
  font-size: 1.8rem;
  color: var(--asideTextColor);
}
.level{
  width: 1.5rem;
  height: 1.5rem;
  border: 1px solid wheat;
  border-radius: 50%;
}

.levelActiveRed{
  background-color:var(--svgMainColor);
}
.levelActiveYellow{
  background-color: var(--svgMainColor);
}
.levelActiveGreen{
  background-color: var(--svgMainColor);
}

@media(max-width: 1200px){
  .level{
    border: 1px solid rgb(92, 91, 91);
  }
}