.fone{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.568);
    z-index: 111;
}
.container{
  position: absolute;
  top: -100vh;

  left: 50%;
  width: 90%;
  max-width: 50rem;
  margin-top: 7rem;
  transform: translateY(-50%) translateX(-50%);

  transition: .5s;
}
.containerActive{
  top: 0%;
}
.img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: red;
  object-fit: contain;
}

@media(min-width:1200px){
  .container{
     width: 50rem;
  }
}