
.container{
  position: relative;
  border-radius: 1rem;
  margin-top: .5rem;
}
.main{
  background-color: var(--mainBgColor);
  color: var(--textMainColor);
  margin-left: 5rem;
  border-radius: 1rem;
  transition: 1.5s;
}

.aside{
  position: fixed;
  top: 50%;
  left: 0;
  width: 5rem;
  height: 100%;
  margin-top: .5rem;
  padding-top: 6rem;
  transform: translateY(-50%);
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
  background-color: var(--asideBgColorSmall);
  border: 4px solid black;
  border-left: none;
  z-index: 35;
  overflow: hidden;
  transition: .25s;
}
.asideAcrive{
  width: 35rem;
}



.asideBtn{
  position: absolute;
  height: 100%;
  width: 5.5rem;
  right: 0;
  top: 0;
  cursor: pointer;
  background-color: var(--svgMainColor);
}
.asideInscript{
  writing-mode: vertical-lr; 
  text-orientation: upright;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  font-size: 2rem;
  font-family: Play;
  font-weight: 800;
}
.asideContainerLittle{
  margin-right: 5.5rem;
}

.contacts{
  flex: 0 0 24rem;;
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-direction: column;
  border-radius: .5rem;
}

.bigContainer{
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.shortContainer{
  padding: 2rem;
  display: flex;
  flex-direction: column;
  gap: .5rem;
}

