.shortContainer2{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.dateContainer{
  display: flex;
  gap: 1rem;
  line-height: 2rem;
  font-size: 1.8rem;
  letter-spacing: .1rem;
}

.dateCont{
  position: relative;
  width: 6rem;
}
.datee{
  position: absolute;
  color: rgb(0, 0, 0);
  white-space: nowrap;
  top: 50%;
  left:10%;
  transform-origin: left ; 
  transform: rotate(90deg) translateY(-50%) translateX(-50%);
}


.dateTitle{
  font-family: Play;
  font-weight: 600;
  margin-bottom: 1rem;
}
.dateContainer ul li{
  position: relative;
  padding-left: 2rem;
  margin-top: .5rem;
}
.dateContainer ul li::before{
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: .1px solid var(--svgMainColor);
  transform: translateY(-50%);
}
.shortContainer3{
  margin-left: 1rem;
  margin-top: 2rem;
}

@media(max-width: 500px){
  .shortContainer2{
    gap: .5rem;
  }
}