.container{
  font-size: var(--fontContacts);
  display: flex;
  align-items: center;
  gap: 1rem;
  /* justify-content: space-around; */
}

.img{
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
}
.img>:first-child{
  position: absolute;
  top: 0;
  left: 0;
  fill: var(--svgAsideColor);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.text{
  font-size: 1.8rem;
  color: var(--textContactsColor);
}

@media(max-width:1200px){
  .img>:first-child{
  fill: var(--svgMainColor);

}
.text{
  color: black;
}
}