.container {
  position: relative;
  width: 100%;
  background-color: var(--blinkOne);
  padding: 0.7rem 0;
  /* animation: changeColor 3s infinite alternate; */
  overflow: hidden;
}

.container h3 {
  text-align: center;
  position: relative;
  font-weight: 800;
  color: var(--asideBgColor)
}


@media(max-width: 1200px){
  .container {
  background-color: var(--svgMainColor);
}
.container h3 {
  color: var(--asideBgColorSmall)
}
}

